:root {
	--white: #FFF8E2;
	--grey: #FFe089;
	--orange-rgb: 255, 202, 61;
	--green-rgb: 114, 114, 59;
	--black-rgb: 40, 23, 1;
	--blue-rgb: 59, 104, 104;
	--red-rgb: 204, 59, 30;
	--orange: rgb(var(--orange-rgb));
	--green: rgb(var(--green-rgb));
	--black: rgb(var(--black-rgb));
	--blue: rgb(var(--blue-rgb));
	--red: rgb(var(--red-rgb));
}

.app {
  text-align: center;
  background-color: var(--white);
  color: var(--black)
}

p {
	margin-left: 50px;
	margin-right: 50px;
}

header {
	top: 0;
	margin:0;
	margin-bottom: 40px;
	padding:0;
	background-color:var(--orange);
	color: var(--black);
}

header > h1 {
	padding: 20px 0px 10px 0px;
	margin: 0;
}

header > p {
	padding: 0px 0px 20px 0px;
}

table {
	border: none;	
	border-collapse: collapse;
	margin: 5px;
}

thead {
	color: var(--black);
	background-color: var(--orange);
	border-radius: 100px;
}

/* Apparently there's a firefox bug which might make this invisible for a bit*/
thead > tr > td:first-child {
	border-radius: 15px 0px 0px 0px;
}

thead > tr > td:last-child {
	border-radius: 0px 15px 0px 0px;
}

tr {
	border-top: 1px solid var(--grey);
}

td {
	padding:5px;
}

th {
	font-weight: normal;
}

button {
	background-color: var(--white);
	border: 1px solid var(--black);
	padding: 15px 32px;
}

.btn-group {
	padding: 5px;
	margin: 10px;
	margin-top: 20px;
}

.btn-group > * {
	margin:auto;
	border-right: none;
}

.btn-group > *:first-child {
	border-radius: 25px 0px 0px 25px;
}

.btn-group > *:last-child {
	border-radius: 0px 25px 25px 0px;
	border: 1px solid var(--black);
}

/* 
 * Several elements can be have a dialect:
 * Spelling
 * Transcription
 * Button
 */


.standard {
	--rgb: var(--black-rgb);
}

.munster {
	--rgb: var(--green-rgb);
}

.connacht {
	--rgb: var(--blue-rgb);
}

.ulster {
	--rgb: var(--red-rgb);
}

.munster.colorblind {
	font-weight: bold;
}

.connacht.colorblind {
	font-style: italic;
}
.ulster.colorblind {
	text-decoration: underline;
}

.dialect {
	--alpha: 0;

	color: rgb(var(--rgb));
	background-color: rgba(var(--rgb), var(--alpha));
}

button.dialect {
	background-color: rgba(var(--rgb), var(--alpha));
	color: rgb(var(--rgb));
	transition: background-color 0.25s;
}

.dialect.on {
	--alpha: 0.2;
}

.spelling-and-transcription {
	padding: 1px;
	margin-top: 5px;
}

.spelling-and-transcription.colorblind {
	--alpha: 0.2;
	border-radius: 2px;
}

.spelling-and-transcription:last-child  {
	margin-bottom: 5px;
}

button.celticist, button.colorblind-setting {
	--rgb: var(--orange-rgb);
	--alpha: 0;
	border: solid 1px var(--black);
	border-radius: 25px;
	background-color: rgba(var(--rgb), var(--alpha));
	transition: 0.5s;
	margin: 5px 10px 10px 10px;
}

.celticist.off, .colorblind-setting.off{
	--alpha: 0;
}

.celticist.on, .colorblind-setting.on {
	--alpha: 0.3;
}

footer {
	background-color: var(--orange);
	padding: 5px;
	margin-top: 10px;
}

